.cartItem {
    width: 100%;
    /* margin-bottom: 8px; */
    min-height: 160px;
    max-height: 170px;
    /* border: 1px solid red; */
    background-color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.item-divider-line {
    height: 0.1px;
    width: 93%;
    margin: 7px auto;
    /* background-color: red; */
    background-color: rgb(187, 187, 187);
}

.cartItem .imgCntnr {
    height: 170px;
    width: 25%;
    /* border: 1px solid red; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.cartItem .imgCntnr img {
    max-height: 100%;
    max-width: 80%;
    /* border: 1px solid blue; */
}

.itemdataCntnr {
    height: 20vh;
    width: 70%;
    /* border: 2px solid blue; */
    display: flex;
    justify-content: space-between;
}

.itemTitleCntnr {
    width: 80%;
    /* border: 0.5px solid red; */
    display: flex;
    flex-direction: column;
    position: relative;
}
.itemTitleCntnr a {
    font-size: 15px;
    color: black;
    text-decoration: none;
    /* border: 1px solid blue; */
}
.itemTitleCntnr a:hover {
    color: rgb(18, 18, 231);
    /* border: 1px solid blue; */
}

.quantity-Incrmnt-Dcrmnt-n-removeCntnr {
    width: 100%;
    /* border: 1px solid blue; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    bottom: 6.5px;
}
.plus-minusCntnr {
    width: 32%;
    min-width: 80px;
    max-width: 130px;
    /* border: 1px solid red; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.plus-minusCntnr button {
    height: 25px;
    width: 26px;
    font-size: 25px;
    font-weight: 200;
    border: 0;
    border-radius: 6px;
    /* border: 1px solid red; */
    background-color: #eaeded;
    box-shadow: 0px 0px 5px -2px black;
    display: flex;
    justify-content: center;
    align-items: center;
}
.plus-minusCntnr button:hover {
    background-color: white;
}

.removebtn {
    padding: 3.5px 7px;
    padding-top: 6px;
    font-size: 16px;
    border: 0;
    border-radius: 7px;
    background-color: #eaeded;
    box-shadow: 0px 0px 5px -2px black;
}
.removebtn:hover {
    /* color: rgb(243, 0, 0); */
    background-color: white;
}

.itemPrice {
    /* display: flex; */
    /* position: absolute;
    right: 10px; */
}
.itemPrice span {
    padding: 10px;
    font-size: 1.08rem;
    font-weight: 500;
    /* border: 1px solid blue; */
}


/* ///////////////////// */
@media (max-width: 905px) {
    .cartItem .imgCntnr {
        /* height: 17.5vh; */
        height: 170px;
    }
    .cartItem .imgCntnr img {
        height: 100%;
    }
}

@media (max-width: 845px) {

}


@media (max-width: 750px) {
    .itemTitleCntnr a {
        font-size: 0.9rem;
    }
    .quantity {
        margin: 0 5px;
    }
    .cartItem .imgCntnr {
        height: 15.8vh;
    }
    .cartItem .imgCntnr img {
        height: 100%;
    }
}

@media (max-width: 675px) {
    .cartItem .imgCntnr {
        height: 15vh;
    }
    .cartItem .imgCntnr img {
        height: 100%;
    }
}


/* //////// css for mobile screen */
@media (max-width: 395px) {
    .cartItem {
        margin: 5px 0;
        padding: 5px 0;
    }
    .cartItem .imgCntnr {
        height: 15vh;
        width: 30%;
        /* border: 0.5px solid black; */
    }
    .cartItem .imgCntnr img {
        height: 100%;
        max-width: 100%;
        /* border: 0.5px solid red; */
    }
    .itemTitleCntnr {
        padding: 0 5px;
    }
    .removebtn {
        padding: 5px 7px;
        padding-top: 6px;
        font-size: 14px;
    }

    .item-divider-line {
        display: none;
    }
    .itemPrice span {
        padding: 5px;
    }
}



/* /////// css for mobile orientation mode */
@media (max-height: 770px) {
    .cartItem .imgCntnr {
        min-height: 165px;
        width: 30%;
        /* border: 0.5px solid black; */
    }
    .cartItem .imgCntnr img {
        height: 100%;
        max-width: 100%;
        /* border: 0.5px solid red; */
    }

    .itemdataCntnr {
        min-height: 165px;
        /* border: 1px solid blue; */
    }
    .itemTitleCntnr {
        padding: 0 5px;
    }
}

